.apps_tab .nav-link.active,
.apps_tab .nav-link:hover {
    border-color: var(--bs-primary) !important;
    color: var(--bs-primary);
}

.maxW80 {
    // max-width: 75%;
    max-height: 265px;
}

.apps_tab .nav-link {
    background-color: var(--bs-body-bg);
    color: var(--bs-body-color);
}

.apps_tab .nav-link div {
    background-color: #F6F9FB;
    border-radius: 4px;
}

[data-theme=dark] .apps_tab .nav-link div {
    background-color: #26272F;
}

.apps_tab .nav-link i {
    font-size: 16px;
}

.apps_tab .nav-link:hover i,
.apps_tab .nav-link.active i {
    color: var(--bs-primary);
}

.custom-api-section {
    background-color: var(--bs-secondary-200);

    button {
        background-color: var(--bs-body-bg);
        width: 200px;
        height: 200px;
    }
}

.selectpicker.__control--is-focused {
    background-color: var(--bs-body-bg);
    border-color: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);
    box-shadow: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);
}

.downloadImages_wrap {

    .css-13cymwt-control,
    .css-1nmdiq5-menu,
    .__control--is--focused {
        background-color: var(--bs-body-bg);
        color: var(--bs-body-color);
        border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);
    }

    .css-1u9des2-indicatorSeparator {
        background-color: var(--bs-body-bg);
    }

    .css-tj5bde-Svg {
        fill: var(--bs-body-color);
    }

    .css-1dimb5e-singleValue {
        color: var(--bs-body-color);
    }
}

@media(max-width:991px) {
    .custom-api-section button {
        width: 120px;
        height: 120px;

        img {
            max-height: 64px;
            width: auto;
        }
    }
}

@media(max-width:574px) {
    .custom-api-section button {
        width: 100px;
        height: 100px;

        img {
            max-height: 54px;
        }
    }
}

@media(max-width:479px) {
    .custom-api-section button {
        width: 80px;
        height: 80px;

        img {
            max-height: 48px;
        }
    }
}